@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
    margin-left: 280px;
    margin-right: 20px;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.card {
    border-radius: 2px;
    display: inline-block;
    transition: all 0.5s ease;
    padding: 10px;
    margin: 1rem;
    position: relative;
    width: 300px;
    /* border-top-right-radius: 30px;
    border-bottom-left-radius: 30px; */
    border-radius: 20px;
    color: blueviolet;
    cursor: pointer;
}

.card:hover {
    /* background: cornsilk; */
    color: blueviolet;
    cursor: pointer;
    box-shadow: 1px 1px 4px 1px;
}

.bgimage {
    top: 10px;
    left: 10px;
    opacity: 10%;
    width: 80%;
    padding: 10px;
    height: 80%;
    position: absolute;
}

.counter-container{
    box-shadow: 1px 1px 1px 1px;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
    background: white;
}



.progress-text {
    text-align: right;
    font-weight: bolder;
}

.progress-text-title {
   text-align: left;
}

.card-detail {
    opacity: 1;
    transition: opacity 1s;
}

.card-detail.hide {
    opacity: 0;
}

/* .card-container{
   
} */
.footer {
    
    position: fixed;
    top: 0px;
    right: 0px;
    text-align: center;
    border-left: 1px solid #f1f1f1;
}

.footer-contact {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-evenly;
    align-items: center;
    color: blueviolet;
    background: cornsilk;
}

.social {
    padding-left: 15px;
    padding-right: 15px;
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
    box-shadow:  3px 0px 3px 0px #f1f1f1;
}

.social:hover{
    background: white;
    padding-top: 20px;
}
.side-bar{
    position: fixed;
    z-index: 100;
    left: 0;
    text-align: left;
    top: 0;
    border-right: 1px solid #f1f1f1;
    width: 280px;
    color: rgb(15, 15, 15);
    height: 100vh;
    overflow-y: auto;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.side-bar::-webkit-scrollbar {
    display: none;
  }

.state-name-item{
    box-shadow: 3px 0px 3px 0px #f1f1f1;
    padding: 15px 25px 15px 25px;
    color: rgb(15, 15, 15);
    transition: all 0.5s ease;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: row-reverse;
    background-color: cornsilk;
}
.state-name-item:hover{
    margin-left:30px;
    cursor: pointer;
    color: blueviolet;
    background-color: white;
}
.state-name-item.active{
    margin-left:30px;
    cursor: pointer;
    color: blueviolet;
    background-color: white;
}

.icon-container{
     padding-left: 10px;
     padding-right: 10px;
}
